<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tab = 1">施工数据</div>
      <div :class="{active:tab == 2}" @click="tab = 2">进度图</div>
    </div>
    <el-table :data="list" border style="width: 100%" v-show="tab == 1">
      <template v-for="(item,i) in arr">
        <el-table-column v-if="item.child" :label="item.txt" :key="i">
          <el-table-column v-for="(l,j) in item.child" :prop="l.prop" :label="l.txt" :key="j"></el-table-column>
        </el-table-column>
        <el-table-column v-else :prop="item.prop" :label="item.txt" :key="i"></el-table-column>
      </template>
    </el-table>
    <div v-show="tab == 2" id="char"></div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import e3d from 'echarts-gl'
  import request from "@/utils/request";
  import {
    schedule
  } from '@/config/well'
  export default {
    props: ["route"],
    data() {
      return {
        tab: 2,
        arr: schedule,
        list: []
      }
    },

    mounted: function () {
      let data = this.route;
      let char =echarts.init(document.getElementById('char'));
      this.tab =1;
      request({
        url: '/data/query/drillingconstructionschedule',
        method: 'post',
        data
      }).then(res => {
        this.list = res;
        this.draw(char,this.setData(res));
      });

    },

    methods: {
      draw(obj,res) {
        obj.setOption({
          title: {
            text: '施工进度图',
            x: 'center'
          },
          xAxis: {
            name: '时间(天)',
            nameLocation: 'end',
            type: 'category',
            data: res[0]
          },
          yAxis: {
            name: '井深(m)',
            nameLocation: 'end',
            type: 'value'
          },
          series: [{
            symbol: "none",
            data: res[1],
            type: 'line'
          }]
        })
      },
      setData(data) {
        let xArr = [[],[]];
        let len =data.length
        for (let i = 0; i < len; i++) {
          xArr[0].push(data[i].operate_days);   //天数
          xArr[1].push(data[i].end_depth);      //井深
        }
        return xArr;
      }
    }
  }

</script>
<style lang="scss" scoped>
  #char {
    height: calc(100% - 50px);
    background: #fff;
  }

</style>



